import React from 'react';
import { Provider } from 'overmind-react';

import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom';

import { ThemeProvider } from '@material-ui/styles';

import { store } from './store/DataProvider';

import theme from './theme';

import Home from './components/Home/Home';
import MainApp from './components/App/App';
import Configs from './components/Configs';
import Sliders from './components/Sliders';
import Users from './components/Users';
import Workers from './components/Workers';
import Clients from './components/Clients';
import Detailed from './components/Detailed';
import Services from './components/Services';
import NotFound from './components/App/NotFound';
import Appointments from './components/Appointments';
import AppointmentDetailed from './components/AppointmentDetailed';

import './styles/styles.scss';

function App() {
  const { user } = JSON.parse(localStorage.getItem('user')) || {user: {}};
  console.log(user.role);
  const gerRoutes = () => user.role === 2 ? (
    <Switch>
      <Route exact path="/" component={Appointments}/>
      <Route exact path="/appointments" component={Appointments}/>
      <Route exact path="/appointments/:id" component={AppointmentDetailed}/>
      <Route component={NotFound}/>
    </Switch>
  ) : (
    <Switch>
      <Route exact path="/" component={Home}/>
      <Route exact path="/configs" component={Configs}/>
      <Route exact path="/users" component={Users}/>
      <Route exact path="/workers" component={Workers}/>
      <Route exact path="/services" component={Services}/>
      <Route exact path="/sliders" component={Sliders}/>
      <Route exact path="/clients" component={Clients}/>
      <Route exact path="/appointments" component={Appointments}/>
      <Route exact path="/appointments/:id" component={AppointmentDetailed}/>
      <Route exact path="/services/detailed/:id?" component={Detailed}/>
      <Route component={NotFound}/>
    </Switch>
  );

  return (
    <Provider value={store}>
      <ThemeProvider theme={theme}>
        <Router basename="/admin">
          <MainApp>
              {gerRoutes()}
          </MainApp>
        </Router>
      </ThemeProvider>
    </Provider>
  );
}

export default App;
