import React, { useState, useCallback } from 'react';
import MaterialTable from 'material-table';

import { makeStyles } from '@material-ui/styles';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExportIcon from '@material-ui/icons/CloudDownload';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
  content: {
    marginTop: theme.spacing(2)
  },
  loading: {
    display: 'flex',
    height: '100%',
    flexGrow: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  textField: {
    padding: theme.spacing(0),
  },
  list: {
    backgroundColor: theme.palette.background.paper,
  },
  hover: {
    '&:hover': {
      backgroundColor: theme.palette.primary.light,
    }
  }
}));

const Workers = () => {
  const classes = useStyles();
  const { state: { users }, actions: { updateUser, addUser, deleteUser }} = useStore();

  const [loading, setLoading] = useState(false);

  const handleSave = useCallback(res => {
    setLoading(true);

    if (res._id) {
      return updateUser({ ...res }).then(() => {
        setLoading(false);
      });
    } else {
      return addUser(res).then(() => {
        setLoading(false);
      });
    }
  }, [addUser, updateUser]);

  const handleDelete = useCallback(id => {
    setLoading(false);
    return deleteUser(id).then(() => setLoading(false));
  }, [deleteUser]);

  const columns = [
    {
      title: 'Name',
      field: 'name'
    },
    {
      title: 'Email',
      field: 'email'
    },
    {
      title: 'Password',
      field: 'password'
    }
  ];

  return (
    <div className={classes.root}>
      <MaterialTable
        title="Worker list"
        columns={columns}
        data={users.map(item => ({ ...item }))}
        icons={{
          Check: CheckIcon,
          Export: ExportIcon,
          Add: AddIcon,
          Edit: EditIcon,
          Delete: CancelIcon,
          Search: SearchIcon,
          Clear: CancelIcon,
          FirstPage: FirstPageIcon,
          NextPage: NavigateNextIcon,
          LastPage: LastPageIcon,
          PreviousPage: NavigateBeforeIcon,
          ResetSearch: CancelIcon,
          SortArrow: ExpandMoreIcon,
        }}
        options={{
          exportButton: true,
          sorting: true,
          search: false,
          addRowPosition: 'first'
        }}
        isLoading={loading}
        editable={{
          onRowAdd: newData => handleSave(newData),
          onRowUpdate: (newData, oldData) => handleSave(newData),
          onRowDelete: oldData => handleDelete(oldData._id),
        }}
      />
    </div>
  );
};

export default Workers;
